.team-container {
    text-align: center;
    padding: 40px;
  

}



/* Tabs */
.tabs {
    display: flex;
    justify-content: space-around;
    justify-content: center;
    margin-bottom: 20px;
    flex-wrap: wrap;
}

.tab {
    background: white;
    border: 2px solid var(--text-dark);
    color: var(--secondary);
    padding: 10px 20px;
    margin: 5px;
    font-size: 16px;
    border-radius: 25px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.tab.active, .tab:hover {
    background: var(--secondary);
    color: white;
    border-color: 2px solid var(--text-dark);
}

/* Team Grid */
.team-grid {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 20px;
}

/* Team Card */
.team-card {
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 180px;
    transition: transform 0.3s ease-in-out;
}

.team-card:hover {
    transform: scale(1.05);
}

/* Team Member Image */
.team-image {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-bottom: 10px;
}

/* Name */
.team-name {
    font-size: 18px;
    font-weight: bold;
    color: var(--text-dark);
}
